<template>
  <div class="ticket">
    <div class="ticket__header">
      <div class="ticket__price">{{ticket.price}} Р</div>
      <div class="ticket__carrier">
        <img class="ticket__image" 
          :src="`http://pics.avs.io/99/36/${ticket.carrier}.png`" 
          :alt="ticket.carrier">
      </div>
    </div>
    <div class="ticket__body">
      <div class="ticket__segment segment-ticket">
        <div class="segment-ticket__column">
          <div class="segment-ticket__name">{{ticket.segments[0].origin}}– {{ticket.segments[0].destination}}</div>
          <div class="segment-ticket__val">{{ticket.segments[0].date | date}} – {{ ticket.segments[0].date | duration(ticket.segments[0].duration) | date}}</div>
        </div>
        <div class="segment-ticket__column">
          <div class="segment-ticket__name">В пути</div>
          <div class="segment-ticket__val">{{ticket.segments[0].duration | hours}}</div>
        </div>
        <div class="segment-ticket__column">
          <div class="segment-ticket__name" v-if="ticket.segments[0].stops.length > 0">{{ticket.segments[0].stops.length}} {{ticket.segments[0].stops.length | declension}}</div>
          <div class="segment-ticket__name" v-else>Без пересадок</div>
          <div class="segment-ticket__val">
            {{ticket.segments[0].stops.join(', ')}}
          </div>
        </div>
      </div>

      <div class="ticket__segment segment-ticket">
        <div class="segment-ticket__column">
          <div class="segment-ticket__name">{{ticket.segments[1].origin}}– {{ticket.segments[1].destination}}</div>
          <div class="segment-ticket__val">{{ticket.segments[1].date | date}} – 00:50</div>
        </div>
        <div class="segment-ticket__column">
          <div class="segment-ticket__name">В пути</div>
          <div class="segment-ticket__val">{{ticket.segments[1].duration | hours}}</div>
        </div>
        <div class="segment-ticket__column">
          <div class="segment-ticket__name" v-if="ticket.segments[1].stops.length > 0">{{ticket.segments[1].stops.length}} {{ticket.segments[1].stops.length | declension}}</div>
          <div class="segment-ticket__name" v-else>Без пересадок</div>
          <div class="segment-ticket__val">{{ticket.segments[1].stops.join(', ')}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Ticket',
  props: {
    ticket:{
      type: Object,
      required: true,
      default: {}
    }
  },
  filters: {
    hours: function (mins) {
      let hours = Math.trunc(mins/60);
      let minutes = mins % 60;
      minutes = minutes.toString().length < 2 ? minutes + '0': minutes;
      
      return hours + 'ч' + ' ' + minutes + 'м';
    },
    date: (value) => {
      const options = {
        hour: '2-digit', 
        minute: '2-digit'
      }
     
      return new Intl.DateTimeFormat('ru-RU', options).format(new Date(value));
    },
    declension: (number) => {
      const cases = [2, 0, 1, 1, 1, 2],
            titles = ['пересадка', 'пересадки'];
      
      return titles[ (number%100>4 && number%100<20)? 2 : cases[(number%10<5)?number%10:5] ];
    },
    duration: (date, dur) => {
      const durationMs = dur / 0.000016666666666666667,
            dateWithDuration =  Date.parse(date) + durationMs;
      
      return new Date(dateWithDuration);
    }
  }
}
</script>

