<template>
  <div id="app">
    <div class="page">
      <header class="header">
        <div class="wrapper">
          <div class="header__content">
            <div class="header__logo">
              <img src="@/assets/img/logo.svg" alt="logo">
            </div>
          </div>
        </div>
      </header> 
      <main class="main">
        <div class="wrapper main__wrapper">
          <nav class="sidebar">
            <div class="sidebar__title">Количество пересадок</div>
              <label class="label"
                v-for="(stop, i) in stops" :key="stop.title">
                <input type="checkbox" class="check-input" 
                  :name="i+'stops'" 
                  v-model="stops[i].state">
                <span class="false-checkbox"></span>
                <span class="check-name">{{stop.title}}</span>
              </label>
          </nav>

          <article class="main__content">
            <div class="main__filter filter-main">
              <button class="filter-main__button"
                v-for="(button, i) in buttons" 
                :key="i"
                @click="buttClick(i)"
                :class="{'active': button.state}">{{button.title}}</button>
            </div>

            <div class="tickets">
              <Preloader v-if="loading"></Preloader>
              <ticket
                v-for="(ticket, i) in ticketsSort" :key="i"
                v-else
                :ticket="ticket"
                ></ticket>
            </div>
            <div class="ticket warning" v-if="serverError">При получении данных с сервера произошла ошибка. Попробуйте еще раз!</div>
            <button class="btn" 
                v-if="!loading"
                @click="updateData">Показать еще 5 билетов!</button>
          </article>
        </div>
      </main>
    </div>
  </div>
</template>

<script>
import Ticket from './components/Ticket.vue'
import Preloader from './components/Preloader.vue'

import {fetchKey, fetchTickets} from './functions/fetch-tickets.js'

export default {
  name: 'App',
  components: {
    Ticket,
    Preloader
  },
   data(){
    return {
      tickets: [],
      stopsCount: '',
      serverError: false,
      searchId: '',
      loading: false,
      resultsCount: 5,
      stops: {
        "0": {
            title: 'Без пересадок',
            state: false
          },
        "1": {
            title: '1 пересадка',
            state: false
          },
        "2": {
            title: '2 пересадки',
            state: false
          },
        "3": {
            title: '3 пересадки',
            state: false
          }
      },
      buttons: [
        {
          title: 'самый дешевый',
          state: true,
          keyWord: 'price'
        },
        {
          title: 'самый быстрый',
          state: false,
          keyWord: 'duration'
        },
        {
          title: 'оптимальный',
          state: false,
          keyWord: 'optimal'
        }
      ]
    }
  },
  methods: {
    buttClick(ind){
      // console.log(this.buttons[ind])
      this.buttons.forEach((b, i, arr) => {
        if(i === ind){
          arr[ind].state = !arr[ind].state;
        }else{
          b.state = false;
        }
        
      });
    },
    async updateData(){
      await fetchTickets(this.searchId, (tickets = [], error = false, loading = false) => {
        this.tickets = tickets;
        this.serverError = error;
        this.loading = loading;
      });
    },
    segmetsOverallStops(ticket){
      return ticket.segments[0].stops.length + ticket.segments[1].stops.length;
    }
  },
  computed: {
    stopsLength(){
      return Object.keys(this.stops).length - 1;
    },
    
    ticketsFilter(){
      let filtered = this.tickets;
      
      if(Object.values(this.stops).some(val => val.state)){
        filtered = this.tickets.filter(ticket => {
          var stops = this.segmetsOverallStops(ticket) >= this.stopsLength ? 
               this.stopsLength : this.segmetsOverallStops(ticket);
          return this.stops[stops.toString()].state;
        });
      }
      
      return filtered.slice(0, this.resultsCount);
    },

    ticketsSort(){
      var ticketsCopy = [...this.ticketsFilter];
      
      if(this.buttons.find(b => b.state)){
        console.log('sort IF condirion')
        let trueElemKeyWord = this.buttons.find(b => b.state).keyWord;
        return ticketsCopy.sort((a,b) => {
          if(trueElemKeyWord === "duration"){
            return (a.segments[0].duration + a.segments[1].duration) - (b.segments[0].duration + b.segments[1].duration);
          }
          if(trueElemKeyWord === "price"){
            return a.price - b.price;
          }
          if(trueElemKeyWord === "optimal"){
            return ((a.segments[0].duration + a.segments[1].duration) - (b.segments[0].duration + b.segments[1].duration)) && (a.price - b.price);
          }
           
        }).slice(0, this.resultsCount);
     
      }else {
        return this.ticketsFilter.slice(0, this.resultsCount) }
      }
  },
  async created(){
    await fetchKey(searchId => {
      this.searchId = searchId;
    });
    
    this.updateData();
  }
}
</script>

<style scoped>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
  /*margin-top: 60px;*/
}

</style>
