const fetchKey = async function(cb) {
  const searchId = await fetch('https://front-test.beta.aviasales.ru/search').then(response => response.json());
  cb(searchId.searchId);
}
const fetchTickets = async function updateData(id, cb) {
  console.log('id in api ' + id);
	try{
      cb([], false, true);
      const tiketsArr = await fetch(`https://front-test.beta.aviasales.ru/tickets?searchId=${id}`).then(response => response.json());
      cb(tiketsArr.tickets, false, false);
        
    }catch(e){
      cb([], true, false);
    }
}
export {fetchKey, fetchTickets}